<template>
	<div class="body">
		<div class="search_group">
			<div class="search_group_top">
				<div class="search_box">
					<el-input v-model="partner_company" prefix-icon="el-icon-search" placeholder="请输入合伙企业名称" clearable>
					</el-input>
				</div>
			</div>
			<div class="search_group_bottom flex_start">
				<div class="search_box">
					<el-input v-model="fundNameShort" placeholder="请输入基金简称" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-input v-model="response_partner" placeholder="请输入合伙人" clearable>
					</el-input>
				</div>
				<div class="search_box">
					<el-input v-model="manager" placeholder="请输入投研经理" clearable>
					</el-input>
				</div>
				<div class="date_box">
					<el-date-picker v-model="start_time" type="date" placeholder="选择开始日期" clearable value-format="yyyy-MM-dd" :editable='false'>
    					</el-date-picker>
				</div>
				<div class="date_middle_text">
					至
				</div>
				<div class="date_box">
					<el-date-picker v-model="end_time" type="date" placeholder="选择截止日期" clearable value-format="yyyy-MM-dd" :editable='false'>
    					</el-date-picker>
				</div>
				<div class="btn_box">
					<button @click="getSubFundList(1)">搜索</button>
				</div>
			</div>
		</div>
		
		<div class="line_box">
			<div class="line"></div>
		</div>
		
		<div class="main under_fund">
			<div class="under_fund_header flex_between">
				<div class="">
					子基金总数<span>(&nbsp;{{total}}&nbsp;)</span>
				</div>
				<div class="flex_end">
					<!-- <button class="hover_btn" @click="downloadExcelData()">
						<span class="iconfont icon-daoru"></span>
						导出excel表格					
					</button>
					<button class="hover_btn" @click="downloadExcelModule()">
						<span class="iconfont icon-daoru"></span>
						下载子基金信息模板
					</button>
					<button class="hover_btn" @click="uploadManageCompanyInfoExcel()">
						<span class="iconfont icon-daochu"></span>
						导入子基金信息
					</button> -->
					<div  @click="downloadExcelData" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>导出excel表格
					</div>
					<div  @click="downloadExcelModule" class="table-group-title-btn btn-blue-border">
						<span class="iconfont icon-daoru"></span>下载子基金信息模板
					</div>
					<div class="">
						<label for="upload-fund-input">
							<div class="table-group-title-btn btn-blue-border">
								<span class="iconfont icon-daochu"></span>导入子基金信息
							</div>
						</label>
						<input type="file" ref="inputer" id="upload-fund-input" style="display:none" v-on:change="uploadManageCompanyInfoExcel($event)" />
					</div>
					<button class="under_fund_header_btn" @click="addNewFund">
						<span class="iconfont icon-jia"></span>
						添加子基金
					</button>
				</div>
			</div>
			<div class="under_fund_main table">
				<el-table :data="subFundListData" border style="width: 100%">
					<el-table-column prop="id" label="序号" width="80">
						<template slot-scope="scope">
							{{ ((currentPage-1)*pageSize)+ scope.$index + 1 }}
						</template>
					</el-table-column>
					<el-table-column sortable prop="name" label="合伙企业名称" width="200">
					</el-table-column>
					<el-table-column sortable prop="id" label="ID" width="80">
					</el-table-column>
					
					<el-table-column sortable prop="short_name" label="基金简称" width="120">
					</el-table-column>
					<el-table-column sortable prop="response_partner_name" label="负责合伙人" width="120">
					</el-table-column>
					<el-table-column sortable prop="manager_name" label="投研经理" width="120">
					</el-table-column>
					<el-table-column sortable prop="subscribeFund" label="认购基金" width="240">
					</el-table-column>
					<el-table-column sortable prop="unit" label="币种" width="100">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column sortable prop="fund_scale" label="基金规模（万元）" width="180">
					</el-table-column>
					<el-table-column sortable prop="agreement_date" label="协议到期日" width="120">
					</el-table-column>
					<el-table-column sortable prop="fund_expire" label="基金期限（年）" width="150">
					</el-table-column>
					<el-table-column sortable prop="renjiao_fee" label="认缴（万元）" width="150">
					</el-table-column>
					<el-table-column sortable prop="real_fee" label="实缴（万元）" width="150">
					</el-table-column>
					<el-table-column sortable prop="mult" label="倍数" width="100">
					</el-table-column>
					<el-table-column sortable prop="compound_mult" label="复核倍数" width="120">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="200">
						<template slot-scope="scope">
							<el-button @click="toFundRunPage(scope.row)" type="text" size="small">基金运行</el-button>
							<el-button @click="view(scope.row)" type="text" size="small">查看</el-button>
							<el-button @click="deleteFund(scope.row)" type="text" size="small">删除</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<el-pagination v-if="total" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    				</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import setPower from '../../components/setPower.vue'
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getSubFundListApi,deleteSubFundApi } from '../../config/api'
	import { ChildrenFundDownloadExcel,ChildrenFundExcelAdd,ChildrenfundExportExcel } from '../../config/apiSj'
	export default {
		name: 'motherFundList',
		data() {
			return {
				currentPage: 1,
				pageSize: 10,
				total: 0,
				partner_company: '',
				fundNameShort: '',
				response_partner: '',
				manager: '',
				start_time: '',
				end_time: '',
				
				noPowerTest: '您没有查看此内容的权限，请联系管理员。',
				subFundListData: [],
			}
		},
		created() {
			this.getSubFundList();
		},
		mounted() {},
		components: {
			setPower
		},
		methods: {
			getSubFundList(is_init_page){
				// 判断结束时间是否大于开始时间
				if (this.start_time > this.end_time) {
					dialogConfirm("info", '结束时间不能小于开始时间');
					return
				}
				if (is_init_page) {
					this.currentPage = 1;
				}
				getSubFundListApi({
					page: this.currentPage,
					pageSize: this.pageSize,
					name: this.partner_company,
					short_name: this.fundNameShort,
					response_partner: this.response_partner,
					manager: this.manager,
					start_time: this.start_time,
					end_time: this.end_time,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.subFundListData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			view(e){
				if (e.auth.check_operate) {
					this.$router.push({
						name: 'subFundDetail',
						query: { id: e.id }
					})
				} else{
					dialogConfirm("info", noPowerTest);
				}
			},
			toFundRunPage(e){
				if (e.auth.auth_operate) {
					console.log(e);
					this.$router.push({
						name: "subFundRun",
						query: {
							id: e.id,
						}
					})
				} else{
					dialogConfirm("info", noPowerTest);
				}
			},
			// 判断删除的时候是否是最后一条记录
			isLeftOneItem(){
				let length = this.subFundListData.length;
				if(length == 1 && this.currentPage > 1){
					this.currentPage = this.currentPage - 1;
				}
			},
			deleteFund(e){
				if (e.auth.delete_operate) {
					var message = "确定要删除所选基金吗"
					dialogConfirmHasbtn(message).then(() => {
						deleteSubFundApi({
							id: e.id
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", res.data.payload);
								this.isLeftOneItem()
								this.getSubFundList()
							} else {
								dialogConfirm("info", res.data.message);
							}
						})
					}).catch(() => {});
				} else{
					dialogConfirm("info", noPowerTest);
				}
			},
			chooseDepartCompanyName(e){
				this.getSubFundList()
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getSubFundList()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getSubFundList()
			},
			addMotherFund(){
				this.$router.push({
					name: "motherFundAdd"
				})
			},
			addNewFund() {
				this.$router.push({
					name:"subFundAdd"
				})
			},


			//导入Excel文件
			uploadManageCompanyInfoExcel() {
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据
				let file = inputDOM.files[0];
				if(file == undefined) {
					return false;
				}
				if(!/\.xl(s[xmb]|t[xm]|am|s)$/.test(file.name)) {
					dialogConfirm('info', '请选择Excel格式文件')
					inputDOM.value = '';
					return false
				}
				ChildrenFundExcelAdd({},file).then(res => {
					if(res.data.msgNo == 1) {
						dialogConfirm('success', '导入成功');
						this.currentPage = 1;
						this.getSubFundList();
					} else {
						dialogConfirm('info', '导入失败，请核对表格数据');
					}
					inputDOM.value = '';
				})
			},
			//导出excel表格
			downloadExcelData(){
				ChildrenfundExportExcel({
					name: this.partner_company,
					short_name: this.fundNameShort,
					response_partner: this.response_partner,
					manager: this.manager,
					start_time: this.start_time,
					end_time: this.end_time,
				}).then(res => {
					if (res.data.msgNo == 1) {
						dialogConfirm("success", '下载成功');
						console.log(res.data.payload);
						window.location.href = res.data.payload.path;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
			},
			//导出基金项目信息模版
			downloadExcelModule(){
				// directDownExcel().then(res => {
				// 	if (res.data.msgNo == 1) {
				// 		dialogConfirm("success", '下载成功');
				// 		window.location.href = res.data.payload;
				// 	}else{
				// 		dialogConfirm("info", res.data.message);
				// 	}
				// })
				// let key = window.localStorage.getItem("htcx_server_key");
				// let href = api.sourceDataUrl + "/web/Company/CompanyInfoExcel?company_id=" + this.motherFundId + '&key=' + key + '&new_type=' + 2;
				ChildrenFundDownloadExcel({}).then(res => {
					if (res.data.msgNo == 1) {
						dialogConfirm("success", '下载成功');
						console.log(res.data.payload);
						window.location.href = res.data.payload;
					}else{
						dialogConfirm("info", res.data.message);
					}
				})
				// let href = window.localStorage.getItem('htcx_server_address') + '/uploads/sg_company_manager/母基金批量导入模版.xlsx'
				// // console.log(href);
				// window.location.href = href;
			},
		},
		
	}
</script>

<style scoped="scoped">
	.body {
		padding: 20px 30px 20px 30px;
		font-size: 14px;
	}
	.search_group_bottom{
		margin-top: 20px;
	}
	.search_group_bottom .search_box{
		width: 140px;
	}
	
	.search_box{
		width: 170px;
		height: 30px;
		margin-right: 13px;
	}
	.date_box{
		width: 162px;
	}
	.date_middle_text{
		margin: 0 10px;
	}
	.btn_box{
		margin-left: 24px;
	}
	.btn_box button{
		width: 80px;
		height: 30px;
		color: white;
		font-size: 14px;
		border-radius: 20px;
		background: #6292F4;
	}
	.under_fund_header {
		margin: 26px 0;
		color: #6292F4;
		font-size: 12px;
	}
	.under_fund_header>div{
		color: #6292F4;
	}
	.under_fund_header span {
		color: #6292F4;
		padding: 0 4px;
	}
	.under_fund_header button{
		/*width: 160px;*/
		height: 30px;
		margin-left: 10px;
		padding: 0 15px;
		color: #6292F4;
		font-size: 14px;
		border-radius: 20px;
		border: 1px solid #6292F4;
		/*background: #6292F4;*/
	}
	.under_fund_header button span{
		color: #6292F4;
		font-size: 14px;
	}
	.under_fund_header_btn{
		color: white !important;
		background: #6292F4 !important;
	}
	.under_fund_header_btn span{
		color: white !important;
	}
	.dialog_btn {
		width: 94px;
		height: 36px;
		color: #6292F4;
		border-radius: 20px;
		border: 1px solid #6292F4;
	}
	
	.dialog_btn_submit {
		margin-left: 20px;
		color: white !important;
		background: #6292F4;
	}
	
	.line_box {
		margin: 30px 0;
	}
	
	.line {
		height: 1px;
		background-color: #E5E5EA;
	}
</style>


